import preload from './scenes/main/preload';
import create from './scenes/main/create';
import update from './scenes/main/update';

import menuScene from './scenes/menu';

var config = {
    type: Phaser.AUTO,
    width: 800,
    height: 600,
    physics: {
        default: 'arcade',
        arcade: {
            gravity: { y: 200 }
        }
    },
};



var game = new Phaser.Game(config);

game.scene.add("menu", menuScene);

game.scene.start("menu");



